<template>
  <div>
    <ul id="example-1">
      <li v-for="(key, val) in items">
        {{ key }} : {{ val }}
      </li>
    </ul>
    <h2 v-if="ok">yes</h2>
    <h2 v-else>no</h2>
  </div>
</template>
<script>
export default {
  data () {
    return {
      items: {
        FirstName: 'John',
        LastName: 'Doe',
        Age: 30
      },
      ok: true
    }
  }
}
</script>



